
@mixin center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
@mixin centerwithoutAlign {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin componentStyle {
    width: 100%;
    height: auto;
    @include center;
    background-color: $genBackCol;
}
