@import 'mixins.scss';

@import url('https://fonts.googleapis.com/css?family=Exo+2|Teko&display=swap');
@import url('https://fonts.googleapis.com/css?family=Rajdhani&display=swap');

html, body  {
    font-family: 'Rajdhani', sans-serif;
    height: 100%;
}
#root{
    height: 100%;
    display: flex;
    flex-direction: column;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
}
h1, h2, h3, h4, h5, h6, button, span, ul, li {
    margin: 0;
    padding: 0;
}
li{
    list-style: none;
}
a {
    text-decoration: none;
}
button, input {
    border: none;
    background: none;
}
.container {
    display: flex;
    width: 100%;
    justify-content: center;
    padding: 0px 15px;
    flex-direction: column;
    box-sizing: border-box;
    
    @media (min-width: 576px) { 
        max-width: 540px;
    }
    @media (min-width: 768px) { 
        max-width: 720px;
    }
    @media (min-width: 992px) { 
        max-width: 960px;
    }
    @media (min-width: 1200px) { 
        max-width: 1140px;
    }
    @media (min-width: 1440px) { 
        max-width: 1440px;
    }
   
}


